.blog-list--container{
    margin: 15px;
    width: 250px;
    height: 213px;
    border-radius: 6px;
    transition: all 0.6s ease-in-out;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.blog-list--container:hover{
 margin-top: -8px;
}

.blog-text{
    height: 153px;
}

.blog-text p {
    text-align: justify;
    width: 100%;
}

.icon-container{
    display: flex;
}

.utility-icon--link{
    display: flex;
    align-items: center;
    color:black;
    width: 50%;
}
.utility-icon--link:hover{
    color: black;
}

.utility-icon{
    display: flex;
    align-items: center;
    cursor: pointer;
    justify-content: center;
    color:#777;
    width: 50%;
    padding: 10px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.utility-icon:hover{
    color:#C89595;
}